<template>
  <!-- 导航栏最外层容器，用于放置scroll-view -->
  <view class="outter">
    <!-- 长度超过屏幕宽度时可左右滑动 -->
    <scroll-view class="nav-container"
      :style="{width: list.length <= 3 ? 100 + '%' : list.length * 100 + 'px'}">
      <!-- scroll-view中不能使用flex布局，所以要再加一层 -->
      <view class="nav-bar">
        <view class="nav-item" v-for="(item,i) in list" :key="i"
          :style="{backgroundColor: toShow === i ? '#20517f' : '#dceffd', color :  toShow === i ? 'white' : '#1b4771', width: list.length <= 3 ? 100 / list.length + '%' : 100 + 'px'}"
          @click="showArea(i)">{{item}}</view>
      </view>
    </scroll-view>
  </view>
</template>

<script>
  export default {
    name: "my-nav-bar",
    props: {
      list: {
        type: Array || null,
        default: []
      }
    },
    data() {
      return {
        // 控制显示哪个区域
        toShow: 0,
      };
    },
    methods: {
      // 控制显示哪个区域
      showArea(i) {
        this.toShow = i
        this.$emit('changeShow',i)
      },
    }
  }
</script>

<style lang="scss" scoped>
  // 导航栏最外层容器，用于放置scroll-view
  .outter {
    margin-bottom: 10px;
    overflow-x: auto;

    // 长度超过屏幕宽度时可左右滑动
    .nav-container {
      padding-bottom: 7px;
      text-align: center;
      width: 470px;

      // 导航栏
      .nav-bar {
        display: flex;
        text-align: center;

        .nav-item {
          height: 36px;
          line-height: 36px;
          border-right: 1px solid #1b4771;
          background-color: #dceffd;
          color: #1b4771;
        }
      }
    }
  }
</style>
